import Notification from "../components/toast";
import Toaster from "../components/toaster";

class ToastService {
  constructor() {
    if (ToastService.exists) {
      return ToastService.instance;
    }
    ToastService.instance = this;
    ToastService.exists = true;
    this.toasts = [];
  }

  addToast(toast) {
    if (this.toasts.length < 3) {
      this.toasts.push(toast);
    }
  }

  success(message) {
    this.addToast({message:message,variant:"success"});
  }

  error(message) {
    this.addToast(<Notification message={message} variant="error" />);
  }

  warning(message) {
    this.addToast(<Notification message={message} variant="warning" />);
  }

  info(message) {
    this.addToast(<Notification message={message} variant="info" />);
  }

  generateToast(...options) {
    return <Notification {...options} />;
  }

  getAllToasts = () => {
    return this.toasts;
  };
}

export const toast = new ToastService();
